}
.warning{
  display: flex;
  background: rgb(253, 207, 207);
  grid-gap: 10px;
  gap: 10px;
  padding: 15px;
  border: 1px solid #c40000;
  border-radius: 5px;
}

#admin_panal details[open] h4{
    color: rgb(255 255 255) !important;
    background: #077bbf !important;
}
#admin_panal details h4{
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.new_order{
  background: #ff2f4c;
  color: #fff;
}
.suc_order{
  background: #007e2d;
  color: #fff;
}
