}
.admin_table{
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 20px;
  gap: 20px;
}
.admin_table .rows > *{
  display: block;
  padding: 10px;
  border: 1px solid darkred;
}
#admin_panal > div{
  display: grid;
  grid-template-columns: 1fr 1.7fr;
  grid-gap: 30px;
  gap: 30px;
}
#admin_panal button.active{
  border: 3px solid black;
} 
.admin_table .suc_order > *{
  border-color: #004600;
}
.admin_table .control{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: none;
  background: rgba(0, 0, 0, 0.308);
  opacity: 0.5;
  transition: opacity 0.1s linear;
}
.admin_table .control:hover{
  opacity: 1;
}
.adminPage_detail_edit{
  position: absolute;
  right: 10px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding:2px 5px;
  background:#fff;
  opacity: 0;
  transition: opacity 0.2s ease;
