.pagination {
  display: flex;
  justify-content: center;
  width: -webkit-max-content;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid #ff1437;
  padding: 5px;
  margin-bottom: 20px;
}
.pagination span {
  background: #142328;
  color: #fff;
  width: 40px;
  height: 33px;
  display: grid;
  place-items: center;
  cursor: pointer;
  box-shadow: 0px 2px 2px 0px #0000005c;
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  font-weight: bold;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}
.pagination span:hover {
  background: #ff1437;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.pagination span.active {
  background: #ff1437;
  color: #fff;
}

